export const tableSearch = (params) => {
    return request({
        url: '/tableSearch',
        method: 'post',
        params
    })
}

export const tableApi = (params) => {
    return request({
        url: '/car/HandleBusHouseld/carList',
        method: 'post',
        params
    })
}

